&__wrapper {
	display: flex;

	&-1 {
		align-items: center;
		min-height: 40px;

		@include adaptive(1023) {
			display: none;
		}

		@include respond('desktop'){
			.header__col {
				&-1 {
					width: 100%;
					max-width: 200px;
					@include adaptive(1799) {
						width: auto;
						max-width: unset;
						padding-right: 15px;
					}
				}

				&-2 {
					flex: 1;
					min-width: 1px;
					max-width: 100%;
					display: flex;
					justify-content: center;
				}

				&-3 {
					display: flex;
					align-items: center;
					justify-content: flex-end;
					width: 100%;
					max-width: 254px;
					@include adaptive(1279) {
						max-width: 194px;
					}

					> div {
						&:not(:first-of-type) {
							padding-left: 4px;
						}
					}
				}
			}
		}


	}

	&-2 {
		min-height: 121px;
		@include adaptive(1023) {
			flex-direction: column;
			min-height: unset;
		}

		.header__col {
			&-1 {
				width: 100%;
				max-width: 410px;

				@include adaptive(1023) {
					max-width: 100%;
				}

				@include respond('ipad_portrait'){
					max-width: 100%;
				}

				@include respond('mobile_landscape'){
					max-width: 100%;
				}
				@include adaptive(768) {
					max-width: 100%;
				}
			}

			&-2 {
				display: flex;
				flex-direction: column;
				flex: 1;
				min-width: 1px;
				max-width: 100%;
				padding-top: 12px;
				padding-bottom: 12px;
				justify-content: center;
				@include adaptive(1023) {
					padding: 0;
					position: relative;
				}
			}
		}
	}

	@include respond('desktop'){
		&-3 {
			align-items: center;
			justify-content: space-between;
		}
	}
}
